.App{text-align:center}.App-logo{height:40vmin;pointer-events:none}@media (prefers-reduced-motion: no-preference){.App-logo{-webkit-animation:App-logo-spin infinite 20s linear;animation:App-logo-spin infinite 20s linear}}.App-header{background-color:#282c34;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.App-link{color:#61dafb}@-webkit-keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}*{font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif}h1{font-size:50px !important;font-weight:800 !important}.font_color{color:#32dd8d}.border_color{color:#32dd8d}#sticky_nav{position:-webkit-sticky;position:sticky;top:0px;z-index:1}.nav_bg{background-color:white}.navbar a{font-weight:700;padding:4px 0;letter-spacing:4px;font-size:12px;margin-left:5px;text-transform:uppercase;font-family:'Exo', sans-serif;transition:0.5s ease-in-out}.navbar a:hover{color:#10391b !important}.active_link{border-top:2px solid #32dd8d !important;border-bottom:2px solid #32dd8d !important}.navbar-brand{font-size:20px !important;font-weight:600 !important;color:#32dd8d !important}#navbar-brand:hover{color:#32dd8d !important}.servicepage_button{letter-spacing:2px;font-size:12px;font-weight:600;position:relative;text-decoration:none;border-radius:25px;transition:0.4s}.servicepage_button:hover{-webkit-transform:translateY(-2px);transform:translateY(-2px)}.link_button{padding:10px 20px;letter-spacing:2px;font-size:12px;font-weight:600;background:transparent;text-decoration:none;color:#32dd8d;border:2px solid #32dd8d !important;border-radius:25px;transition:0.4s}.link_button:hover{background-color:#32dd8d;color:white}#home_img{position:relative;animation:up-down 2s ease-in-out infinite alternate-reverse both}@-webkit-keyframes up-down{0%{-webkit-transform:translateY(10px);transform:translateY(10px)}100%{-webkit-transform:translateY(-10px);transform:translateY(-10px)}}@keyframes up-down{0%{-webkit-transform:translateY(10px);transform:translateY(10px)}100%{-webkit-transform:translateY(-10px);transform:translateY(-10px)}}.card{border:none !important}.card-title{font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;letter-spacing:2px;font-size:small;font-weight:700}.card-text{font-family:"Segoe UI",Tahoma,Geneva,Verdana,sans-serif !important}.form-control{font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;background-color:rgba(50,221,141,0.1) !important;border:none !important;border-bottom:2px solid #3bf8a0 !important}.input-group-text{border:none !important;border-bottom:2px solid #32dd8d !important;background-color:rgba(50,221,141,0.2) !important;font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif}.social_icons{font-size:30px !important;padding:7px;color:#282c34;border-radius:50%;cursor:pointer;box-shadow:0 1px 3px black !important;transition:0.5s !important}.social_icons:hover{-webkit-transform:scale(1.2);transform:scale(1.2);box-shadow:none !important}.footer{width:100%;padding:5rem 0;font-size:13px;letter-spacing:2px;color:#2c2828}

